import React from 'react';
import { PanelProps } from '@grafana/data';
import { SimpleOptions } from 'types';
import JSONPretty from 'react-json-pretty';
import 'react-json-pretty/themes/adventure_time.css';

interface Props extends PanelProps<SimpleOptions> {}

export const SimplePanel: React.FC<Props> = ({ options, data, width, height }) => {
  const { fieldName } = options;
  const frame = data.series[0];
  const valueField = frame.fields.find(field => field.name === fieldName);

  return (
    <div style={{ overflow: 'auto', width, height }}>
      {valueField
        ? valueField.values.toArray().map(value => {
            const displayValue = valueField.display!(value).text;
            return <JSONPretty id="json-pretty" data={displayValue}></JSONPretty>;
          })
        : null}
    </div>
  );
};
